        toes or genitals, as it sits hunched in the corner farthest from the
        bucket and the two mops. It is afraid of the mops. It finds them
        horrible. It shuts its eyes, but it knows the mops are still standing
        there; and the door is locked; and nobody will come. The door is always
        locked; and nobody ever comes, except that sometimes--the child has no
        understanding of time or interval--sometimes the door rattles terribly
        and opens, and a person, or several people, are there. One of them may
        come in and kick the child to make it stand up. The others never come
        close, but peer in at it with frightened, disgusted eyes....<br /><br />They
        all know it is there, all the people of Omelas. Some of them have come
