    <p>
      Like the dams and the fossil-fuel system, Hanford is a legacy of
      twentieth-century modernism. And like the dams and the fossil-fuel system,
      it still fulfills its opulent promises for some people, even though it is
      increasingly perceived by others as a threat.
    </p>

    <p>
      The dams offer hydroelectric power and industrial development to most of
      the Columbia Basin, while endangering the salmon and the cultural life of
      the Native American tribes. The fossil-fuel system makes astonishing
      contributions to human mobility, while placing the entire living planet in
      jeopardy. Both of these are regional and planetary scale issues. Hanford
      covers a much smaller territorial footprint, yet still it appears as a
      hyperbolic case, because the fission products that sprang from its short
      wartime fever will contaminate the soil and water for incommensurable
      periods, stretching hundreds of thousands of years into the future.
    </p>

    <p>
      Take the extreme end of the spectrum: iodine-129. It constitutes one of
      the largest groundwater plumes on the Central Plateau and has a half-life
      of 15.7 million years. Toward the other end of the temporal spectrum,
      strontium-90, which is found near the river in particular, has a half-life
      of only 8.8 years. Somewhere in the middle is plutonium-239, whose
      production was Hanford's sole mission. Its half-life is a comparatively
      modest twenty-four thousand years&mdash;a lapse of time which remains
      thoroughly unimaginable in traditional human terms.
    </p>

    <p>
      Let's take a temporal landmark with which inhabitants of the Pacific
      Northwest have become somewhat familiar: the 
        <span
        class="maplink"
        @click="
          emit('set-map-view', { center: [-119.012, 46.3], resolution: 900 })
        "
        >Ice Age Floods</span
      >, whose
      violently rushing waters carved the Willamette Valley and the impressive
      canyons, mesas, and dry waterfalls of Washington's Channeled Scablands.
      The floods were released by the periodic collapse of ice dams that held
      back the meltwaters of glacial Lake Missoula. The inundations occurred
      some thirteen to fifteen thousand years ago, well before the domestication
      of animals or the invention of agriculture. These tumultuous floods entirely covered the Hanford site,
      not once but many times. Can we imagine what might happen to the remaining
      nuclear wastes in the next fifteen millennia&mdash;or even in the next 150
      years?
    </p>
    <a
      href="https://www.columbiariverkeeper.org/sites/default/files/2018-07/2018.6.1%20Hanford%20Vision%20Report_j_INTERACTIVE.pdf"
      target="_blank"
      rel="nofollow"
      ><img src="images/CompetingVisions.png" class="fullwidth"
    /></a>
    <div class="caption">Click the image for the pdf report<br /></div>

    <p>
      The "seventh generation" is a measure that Native Americans often use to
      describe the stewardship responsibilities incurred by the harvest of
      animals and plants, or by any other kind of exploitation. The idea is to
      use the fruits of the land in a way that respects the plants and animals
      and leaves equal benefit for others, as far down as the seventh
      generation. Seven generations is roughly 150 years&mdash;a long time for
      settlers whose North American ancestors might not even go back that far.
      Still, it remains an imaginable period of time for any human being.
    </p>

    <p>
      In 1999, the State and Tribal Working Group authored a report entitled
      <a
        href="https://www.energy.gov/sites/prod/files/2018/07/f53/STGWG_Closure_for_7th_Gen_Report_%282017%29.pdf"
        target="_blank"
        rel="nofollow"
        >Closure for the Seventh Generation</a
      >, which spells out local views of the standards to which the federal
      Department of Energy should adhere in its environmental cleanup and
      eventual handover of contaminated nuclear sites. In 2018 Dan Serres,
      conservation director for Columbia Riverkeeper, drew inspiration from the
      latest version of that report to assess DOE's most recent plans for the
      Hanford site.
    </p>

    <p>
      The state and tribal report stresses the fragility of so-called
      "institutional controls," such as land-use laws, KEEP OUT signs, or even
      engineered fences and barriers that may not withstand the test of time.
      This fragility matters a great deal, because in the future, people will
      clearly want to use this land for many reasons. In the Columbia
      Riverkeeper report, Dan Serres stresses the high value that 
        <span
        class="maplink"
        @click="
          emit('set-map-view', { center: [-119.516, 46.715], resolution: 7 })
        "
        >large tracts of cleared land along a free-flowing stretch of the Columbia River</span
      > will hold for Native Americans in particular, and indeed, for everyone. Will
      guards be funded, will fences remain standing, will future users of the
      site even notice rusting signs that once read DANGER or KEEP OUT?
    </p>

    <img src="images/HanfordSign.jpg" class="fullwidth" />

    <p>
      The question is crucially important because DOE now envisages reserving
      certain portions of the Hanford site for "industrial uses only," a
      restriction that depends on the maintenance of institutional controls and
      does not take into account either the desire of Native Americans to
      inhabit lands that were traditionally theirs, nor the use of riverine
      landscapes by visitors from nearby cities seeking recreation and another
      perspective on life. The same question becomes even more relevant in light
      of recently announced DOE plans to reclassify much of the radioactive
      debris currently stored in leaking tanks on the Central Plateau as
      "low-level waste," and then simply pour concrete over it and leave it to
      the elements. Apparently the seventh generation does not exist for DOE,
      which nonetheless holds public responsibility for materials whose toxicty
      is known to last for millennia.
    </p>

    <p>
      Tribes, citizens and lawmakers in the states of Washington and Oregon, and
      indeed, throughout Cascadia, have a very different vision of the natural
      environment that they would like to enjoy and to leave to their children
      and grandchildren, down to or even beyond the seventh generation. The
      damming of the Columbia River and its tributaries, as well as the current
      reckless exploitation of North American fossil-fuel reserves, can make
      that vision appear as a cruel and hypocritical lie, no different in
      substance from DOE's transparently inadequate plans for the future of
      Hanford. Yet it's not a lie and it's not hypocrisy to believe in what
      ought to be. Everything now depends on those who are ready to act on their
      belief, and contribute to the struggle of tribal and state representatives
      who do not accept the currently inadequate federal dictates. Art, science,
      activism, education, the courts, the voting booths and the media are all
      avenues for the beginning of a systemic change, which must ultimately be
      embodied in our concrete economic life and in our day-to-day behaviors as
      users of vulnerable and damaged lands.
    </p>

    <p>
      Hanford, in particular, is both a real-life ecological disaster and a
      potent symbol of the responsibility that we all have when it comes to
      dealing with the legacies of twentieth-century modernism. That
      responsibility informs the name of one of the key groups now working with
      the problem: a group called The Hanford Challenge. The capacity to stick
      with such a challenge, despite all its difficulties, is one of the most
      important things that can be learned from ecological initiatives in
      Cascadia. At stake is a reinterpretation and fresh use of the human
      energies that created the problem in the first place. If three nuclear
      reactors and an entirely new process for the extraction of plutonium from
      spent fuel could be developed in the lapse of only two years, then why
      can't we now find a way to face and undo the mistakes that were part and
      parcel of past successes?
    </p>

    <p>
      The answer that we give to this question today and over the upcoming
      decades will be remembered and lived every day by the seventh generation.
    </p>
